// Mobile menu
.mobile-menu {
  @media (min-width: $xl) {
    //display: none;
  }

  // Burger
  &__btn {
    &-burger {
      width: 20px;
      cursor: pointer;

      @media (min-width: $md) {
        width: 30px;
      }

      @media (min-width: $xl) {
        display: none;
      }

      rect {
        transition: $transition;
      }

      &:hover {
        rect{
          &:first-child {
            width: 30px;
          }

          &:nth-child(2) {
            width: 20px;
          }

          &:nth-child(3) {
            width: 10px;
          }

          &:nth-child(4) {
            width: 15px;
          }

          &:nth-child(5) {
            width: 25px;
          }
        }
      }
    }

    &-text {
      @media (max-width: $max-xl) {
        display: none;
      }

      @media (min-width: $xl) {
        display: flex;
        gap: 5px;
        font-size: 12px;
        cursor: pointer;
        transition: $transition;
      }

      @media (min-width: $xxl) {
        font-size: 14px;
      }

      @media (any-hover: hover) {
        &:hover {
          color: $secondary-clr;
        }
      }

      &:hover {
        .mobile-menu__btn-text-icon {
          transform: rotate(45deg);
        }
      }

      &-icon {
        @media (min-width: $xl) {
          width: 10px;
          color: $secondary-clr;
          transition: $transition;
        }

        @media (min-width: $xxl) {
          width: 13px;
        }
      }
    }
  }

  // Menu
  &__menu {
    transform: translateX(-100%);
    position: absolute;
    top: 0;
    left: 0;
    width: 80vw;
    height: 100vh;
    background-color: $bg-clr;
    transition: $transition;

    @media (min-width: $md) {
      width: 50vw;
    }
  }
}

.is-open {
  .mobile-menu__menu {
    transform: translateX(0);
  }
}
